"use client"
import React from 'react'
import Header from './_components/header'
import Sidebar from './_components/sidebar'
function DashboardLayout(
    {children}
    : {
        children: React.ReactNode;
    }
) {
  return (
    <div>
        <Header />
        {children}
    </div>
  )
}

export default DashboardLayout